.teachers {
    .article--hero_left {
        @include article-hero-left(url("/img/article/teachers-hero.jpg"));
    }
}

.teacher-cards {
    &_grid {
        display: grid;
        grid-gap: 24px;
        grid-template-columns: repeat(auto-fit, minmax(360px, 1fr));
    }
}

.teacher-card {
    padding-bottom: 1.5em;

    &-info {
        margin-top: 1em;

        h2 {
            font-size: 18px;
            font-weight: 700;
            color: #5c6161;
            text-align: center;
        }

        p {
            font-size: 15px;
            font-weight: 500;
            color: #5c6161;
            text-align: center;
        }
    }

    &-read-more {
        hr {
            margin: 15px auto;
            width: 50%;
            background: #4b5098;
        }

        &_link {
            display: flex;
            justify-content: center;

            img {
                margin: 0 1em;
            }
        }
    }

    &-quote {
        p {
            margin: 0 auto;
            max-width: 205px;
            font-size: 15px;
            color: #3e4040;
            text-align: center;
        }
    }
}
